import { useState, useEffect } from "react";
import { Card } from "./components/ui/Card";
import React from "react";

export default function InfiniteScrollPage() {
  const [posts, setPosts] = useState([]);
  const [loading, setLoading] = useState(false);
  const [page, setPage] = useState(1);

  useEffect(() => {
    loadMorePosts(); // Load first batch
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, [handleScroll]); // Ensure it updates properly

  const handleScroll = () => {
    if (
      window.innerHeight + document.documentElement.scrollTop >=
      document.documentElement.offsetHeight - 10 // 10px buffer to trigger earlier
    ) {
      loadMorePosts();
    }
  };

  const loadMorePosts = () => {
    if (loading) return;
    setLoading(true);

    setTimeout(() => {
      const newPosts = Array.from({ length: 10 }, (_, i) => ({
        id: posts.length + i + 1,
        text: `ðŸš€ Post #${posts.length + i + 1}: Discovering the World of Data Science`,
        details: `Data science is transforming industries by enabling businesses to make data-driven decisions. From machine learning to AI, the field continues to grow rapidly. Post #${posts.length + i + 1} dives into an exciting topic!`,
      }));

      // Insert the secret clue at post #25
      if (posts.length + 1 === 25) {
        newPosts[0].text = "ðŸ•µï¸â€â™‚ï¸ Clue: The treasure lies in the world of LEGO!";
        newPosts[0].details = "Download the LEGO Database dataset from Kaggle and uncover the secrets hidden within the bricks.";
        newPosts[0].link = "https://www.kaggle.com/datasets/rtatman/lego-database";
      }

      setPosts((prev) => [...prev, ...newPosts]);
      setLoading(false);
    }, 1000);
  };

  return (
    <div className="p-5">
      <h1 className="text-2xl font-bold mb-4">ðŸ“œ Infinite Scrolling Clue Hunt</h1>
      <p className="text-gray-700 mb-6">
        Scroll down to reveal new posts. Hidden within one of them is your next clue! ðŸ”Ž
      </p>
      <div className="space-y-5">
        {posts.map((post) => (
          <Card key={post.id} className="p-5 bg-gray-100 border border-gray-300 rounded-lg shadow-lg">
            <h2 className="text-lg font-semibold">{post.text}</h2>
            <p className="text-gray-600">{post.details}</p>
            {post.link && (
              <a href={post.link} target="_blank" rel="noopener noreferrer" className="text-blue-500 block mt-2">
                ðŸ”— Get the Dataset Here
              </a>
            )}
          </Card>
        ))}
      </div>
      {loading && <p className="text-gray-500 mt-6 text-center">Loading more posts...</p>}
    </div>
  );
}
